import { Component, OnInit, Input, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';

@Component({
    selector: 'jui-badge',
    template: `
      <div class="jui-badge">
          <ng-content select="[badge-body]"></ng-content>
          <span *ngIf="displayType === 'text' && (displayNum || showZero)" class="display-text" [innerHTML]="displayNum" [style.background-color]="bgColor" [style.color]="fontColor"></span>
          <span *ngIf="displayType === 'dot' && showDot" class="display-dot" [style.background-color]="bgColor"></span>
          <span *ngIf="displayType === 'no-wrapper' && (displayNum || showZero)" class="display-no-wrapper" [innerHTML]="displayNum" [ngStyle]="styleObj" ></span>
      </div>
    `,
    styles: [`
      .jui-badge {
          position: relative;
          box-sizing: border-box;
          padding: 0;
          margin: 0;
          display: inline-block;
      }
      :host /deep/ .jui-badge>div {
          display: inline-block;
      }
      .jui-badge .display-text {
          display: inline-block;
          padding: 0 6px;
          border-radius: 10px;
          height: 20px;
          text-align: center;
          line-height: 20px;
          min-width: 20px;
          position:absolute;
          top: -10px;
          left: 100%;
          -webkit-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
          transform: translateX(-50%);
          -webkit-transform-origin: -10% center;
          -ms-transform-origin: -10% center;
          transform-origin: -10% center;
      }
      .jui-badge .display-dot {
          display: inline-block;
          border-radius: 50%;
          height: 8px;
          width: 8px;
          position:absolute;
          top: -4px;
          left: 100%;
          -webkit-transform: translateX(-50%);
          -ms-transform: translateX(-50%);
          transform: translateX(-50%);
          -webkit-transform-origin: -10% center;
          -ms-transform-origin: -10% center;
          transform-origin: -10% center;
      }
      .jui-badge .display-no-wrapper {
          display: inline-block;
          position: inherit;
          padding: 0 6px;
          border-radius: 10px;
          height: 20px;
          text-align: center;
          line-height: 20px;
          min-width: 20px;
          background-color: #f04134;
          color: white;
      }
      /*.jui-badge.success .display-num {*/
          /*background-color: #00a854;*/
      /*}*/
      /*.jui-badge.error .display-num {*/
          /*background-color: #f04134;*/
      /*}*/
      /*.jui-badge.warning .display-num {*/
          /*background-color: #ffbf00;*/
      /*}*/
      /*.jui-badge.info .display-num {*/
          /*background-color: #108ee9;*/
      /*}*/
    `]
})

export class BadgeComponent implements OnInit {
    public displayNum: any;
    @Input()
    public maxNum: number;
    @Input()
    public showZero: boolean = false;
    @Input()
    public showDot: boolean = true;
    @Input()
    public set badgeNum (value) {
        if ( this.maxNum && value >= this.maxNum ) {
            this.displayNum = (this.maxNum - 1 ) + '+';
        } else {
            this.displayNum = value;
        }
    }
    @Input()
    public displayType: string = 'text'; // text/dot/no-wrapper
    @Input()
    public bgColor: string = '#f04134';
    public bgColorList = {
        error: '#f04134',
        success: '#00a854',
        warning: '#ffbf00',
        info: '#108ee9'
    };
    @Input()
    public fontColor: string = '#ffffff';
    @Input()
    public styleObj: any;

    constructor() {
        // pass
    }

    public ngOnInit() {
        let patt = new RegExp(/^#([0-9a-f]{3}){1,2}$/i); // test hex color
        !patt.test(this.bgColor) ? this.bgColor = this.bgColorList[this.bgColor] : '';
    }

}
